.content-top {
	background: url("../images/catalog.jpg") no-repeat;
	background-size: 100% 100%;
	max-width: 1436px;
	margin: 0 auto;
	min-height: 362px;
	display: flex;

	&__inner {
		width: 100%;
		// height: 100%;
		display: flex;
		flex-wrap: wrap;
		align-items: baseline;
		align-content: baseline;
	}

	&__tags {
		.content-top__aside {
			max-width: 463px;
			padding: 44px 41px 40px 33px;
		}

		.content-top__title_top {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 30px;
			font-weight: 400;
			line-height: 37px;
			text-transform: uppercase;
			margin-bottom: 9px;
			display: flex;
			flex-direction: column;
		}

		.content-top__title_bottom,
		span {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 16px;
			font-weight: 400;
		}
	}

	.container {
		padding: 0;
		display: flex;
		flex-direction: column;
		height: 100%;
	}

	&__aside {
		border-radius: 3px;
		background-color: #394791c4;
		padding: 32px 65px 32px 32px;
		max-width: 572px;
		margin-bottom: 32px;
	}

	&__title {
		width: 100%;


		&_top {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 32px;
			font-weight: 400;
			line-height: 1.1;
			text-transform: uppercase;
			margin-bottom: 18px;

			span {
				color: #ffffff;
				font-family: "Roboto Condensed";
				font-size: 16px;
				font-weight: 400;
			}
		}

		&_bottom {
			// margin-bottom: 91px;
			// width: 470px;
			color: #ffffff;
			font-family: Arial;
			font-size: 13px;
			line-height: 22px;
		}
	}

	.breadcrumbs {
		// width: 100%;
		font-size: 12px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 2;
		letter-spacing: normal;
		color: #ffffff;
		margin-bottom: 10px;
		margin-top: 25px;
		background: rgba(0, 0, 0, 0.21);
		width: auto;
		align-self: baseline;
		padding: 0 14px;

		div {
			align-items: center;
		}

		// a {
		// 	// color: #ffffff;

		// 	color: #ffffffc9;
		// }
		a {
			// color: #ffffff;

			color: #fff;
		}


		&__link {
			color: #fff;
		}

	}


	@media screen and (max-width: 767px) {
		height: auto;
		background-position-x: 100%;
		background: url("../images/catalog-mobile.png") 100% 100% no-repeat;
		background-size: cover;
		min-height: 400px;

		.container {
			height: 100%;
			display: flex;
			flex-direction: column;
			// justify-content: center;
		}

		.page__title.content-top__title_bottom {
			display: none;
		}

		.navchain {
			font-family: Arial;
			font-size: 12px;
			line-height: 24px;
			margin-top: 146px;
			text-align: center;
		}

		&__aside {
			background: unset;
			width: 100%;
			padding: 0;
			max-width: unset !important;
		}


		&__title_top {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 30px;
			font-weight: 400;
			line-height: 39px;
			text-transform: uppercase;
			// word-spacing: 60rem;
			text-align: center;
			width: 100%;
			background: none;

		}

		&__title_bottom {
			display: none;
		}

		&__inner {
			background: #00000030;
		}

		.breadcrumbs {
			margin-top: 145px;
			text-align: center;
			padding: 0 16px;

			div[itemtype="http://schema.org/BreadcrumbList"] {
				justify-content: center;
			}
		}

		&__tags {
			.content-top__aside {
				padding: 0;
			}

			.breadcrumbs {
				// padding: 0;
				margin-top: 44px;
			}

			.content-top__title_top {
				color: #ffffff;
				font-family: "Roboto Condensed";
				font-size: 30px;
				font-weight: 400;
				line-height: 39px;
				text-transform: uppercase;
				margin-bottom: 14px;
			}

			.content-top__title_bottom {
				display: flex !important;
				justify-content: center;
			}
		}
	}

	@media screen and (max-width: 480px) {
		background: #394791 !important;
		min-height: unset;

		p {
			&:not(:first-of-type) {
				display: none;
			}
		}

		.page__title.content-top__title_bottom {
			display: block;

			&.active {
				p {
					&:not(:first-of-type) {
						display: block;
					}
				}
			}
		}

		.breadcrumbs {
			margin-top: 0;

			&__link {
				color: #ffffff;
			}

		}

		&__inner {
			background: none;
			padding-top: 18px;
		}

		&__title_top {
			margin-bottom: 15px;
		}

		&__switch {
			color: #ffffff;
			font-family: "Roboto Condensed";
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;
			text-transform: uppercase;
			border-bottom: 1px dashed #ffffff;
			width: max-content;
		}


	}
}
