.catalog,
.seach-found {
	width: calc((100% / 12) * 9) !important;
	margin-left: auto;
	padding-top: 23px;
	margin: 0 !important;
	padding-left: 7px;

	* {
		transition: 0.4s;
	}

	&__sorting {
		display: flex;
		margin-bottom: 25px;
		// justify-content: space-between;
		// flex-grow: 1;
		font-family: Arial;
		font-size: 14px;
		font-weight: 700;
		line-height: 24px;
		text-align: left;
		color: #333333;
	}

	.catalog__sale {
		margin-top: unset !important;
		margin-bottom: 8px;
		padding-right: 3px;
		height: 36px;
		align-items: flex-end;
		flex-grow: 1;

		.catalog__price {
			width: calc(100% - 112px);
			height: 100%;
			align-items: flex-end;
			padding-top: 2px;
			line-height: 1;
			padding-bottom: 10px;
		}

		.special-offers__rub {
			line-height: 1;
		}
	}

	.seach-found {}

	.catalog__text {
		padding: 15px 0 2px !important;
		min-height: 95px;
		flex-shrink: 0;
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 18px;
		font-weight: 400;
		line-height: 26px;
	}

	&__prop {
		display: flex;
		align-items: flex-end;
		padding: 0px;
		flex-grow: 99;
		flex-wrap: wrap;

		margin-bottom: 5px;
		margin-top: 0px;

		.link-pop-glossary {}

		.icons-prop-item {
			width: 38.94px;
			height: 39px;
			padding: 0 4px 4px 0;
		}
	}

	.sorting__info {
		display: flex;
		margin-left: auto;
	}

	.sorting__number {

		font-size: 13px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: normal;
		letter-spacing: normal;
		text-align: left;
		color: #333333;
		display: flex;
		align-items: center;

		&:first-child {
			padding-right: 5px;
		}
	}

	&__ul {
		display: flex;
		list-style: none;
		margin-bottom: 0;
		align-items: center;
		padding: 0;
		color: #333333;
		font-family: Arial;
		font-size: 14px;
		font-weight: 700;
		line-height: 24px;
		text-align: left;
		color: #333333;
		padding-left: 7px;
	}

	&__li {
		margin-right: 16px;
		display: flex;
		height: 28px;
		position: relative;

		&:hover {
			.catalog__sort {

				background-color: #394791;

				span {
					color: #ffffff;
				}
			}

		}

		&:after {
			background: url('data:image/svg+xml; charset=utf-8;base64,') no-repeat -3px 2px/15px;
			width: 20px;
			height: 20px;
			content: '';
			position: absolute;
			right: 0;
			top: 16px;
			display: block;
			margin-top: -12px;
			-webkit-transform: rotate(0);
			transform: rotate(0);
			-webkit-transition: -webkit-transform 500ms;
			transition: -webkit-transform 500ms;
			transition: transform 500ms;
			transition: transform 500ms, -webkit-transform 500ms;

		}

		&.active {
			.catalog__sort {
				background-color: #394791;

				span {
					color: #ffffff;
				}
			}

		}

		&.sorting__link_up {
			&:after {
				background: url('data:image/svg+xml; charset=utf-8;base64,') no-repeat 5px 2px/15px;
				-webkit-transform: rotate(180deg);
				transform: rotate(180deg);

			}
		}

		&:first-child {
			margin-left: 29px;
		}

		&:last-child {
			margin-right: 0;
		}
	}

	&__sort {
		padding: 4px 28px 4px 8px;
		font-weight: normal;
		color: #394791;
		border-radius: 2px;
		background-color: #f1f1f1;
		text-transform: capitalize;
		display: flex;
		align-items: center;
		font-family: Arial;
		font-size: 14px;
		line-height: 20px;
		text-decoration: none !important;
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 0px;

		.special-offers__rub {
			line-height: 1;
		}
	}

	&__item {
		width: calc(((100% / 12) * 4) - 18px);
		height: fit-content;
		margin-bottom: 24px;
		padding: 30px 19px 22px;
		position: relative;
		border: 1px solid #f1f1f1;
		display: flex;
		flex-direction: column;
		flex: 1 0 calc(33.33333% - 18px);
		min-width: 240px;
		box-shadow: 0 2px 21px rgba(0, 0, 0, 0);
		max-width: calc(33.33333% - 18px);
		height: auto;

		&:hover {
			box-shadow: 0 2px 21px rgba(0, 0, 0, 0.1);
		}

		&:nth-child(odd) {}

		.announcing {
			width: 24px;
			position: absolute;
			top: 19px;
			left: 20px;
			background-size: cover;
			background-position: -4px 0;
			display: flex;
			flex-direction: column;

			&__item {
				display: block;
				height: 24px;
				margin-bottom: 5px;

				img {
					width: 100%;
					height: 100%;
				}
			}
		}

		&:nth-child(3n-1) {
			margin: 0 26px 24px;
		}
	}

	&__pic {
		width: 100%;
		display: flex;
		height: 250px;
		// flex-shrink: 0;
		justify-content: center;
		flex-direction: column;
		align-items: center;

		img {
			max-width: 100%;
			height: auto;
			max-height: fit-content !important;
			width: auto;
		}
	}

	&__specification {
		display: flex;
		flex-wrap: wrap;
		margin: 0 0 14px 0;
		flex-shrink: 0;
		padding-right: 3px;
		// height: 20%;
	}

	&__feature {
		display: flex;
		color: #666666;
		font-family: Arial;
		font-size: 12px;
		line-height: 14px;
		width: 100%;
		justify-content: space-between;
		margin-bottom: 8px;
		align-items: flex-end;

		&:last-of-type {
			margin-bottom: 0px;
		}
	}

	&__bold {
		min-width: 80px;
		text-align: right;
		font-family: Arial;
		font-weight: 700;
	}

	&__existence {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-wrap: wrap;
		height: 20px;
		flex-shrink: 0;
		margin-top: 0px;
		padding: 0 15px 0 5px;
	}

	&__presence {
		color: #6ba86e;
		font-family: Arial;
		font-size: 12px;
		line-height: 1.8;
		margin-bottom: 2px;
		display: flex;
		align-items: flex-end;
		// flex-grow: 1;
		flex-shrink: 0;
	}

	.grey {
		color: #666666;
	}

	&__oneclick {
		color: #394791;
		font-family: Arial;
		font-size: 12px;
		border-bottom: 1px dashed #394791;
		text-decoration: none;
		line-height: normal;
		flex-shrink: 0;
		margin: 2px 0 2px auto;

		&:hover {
			text-decoration: none;
		}
	}

	&__simile {
		background: url("../images/compare.svg") no-repeat;
		background-position: top center;
		width: 15px;
		height: 18px;
		color: transparent;
		overflow: hidden;

		&.active {
			background: url("../images/compare-color.svg") no-repeat;
			background-position: top center;
			background-size: contain;
		}
	}

	&__favorite {
		background: url("../images/fav_inactive.svg") no-repeat;
		background-position: top center;
		background-size: contain;
		margin-left: 22px;
		margin-right: auto;

		&.active {
			background: url("../images/fav_active.svg") no-repeat;
			background-position: top center;
			background-size: contain;
		}

		width: 18px;
		height: 18px;
	}

	.stock {
		padding: 0;

		&__info {
			position: absolute;
			bottom: 0;
			top: 0;
			left: 0;
			right: 0;
			padding: 0 18px 24px;
			display: flex;
			flex-wrap: wrap;
			align-content: flex-end;
			align-items: flex-end;
			text-align: center;
			justify-content: center;
		}

		&__title {
			width: 100%;
			color: #f8f8f8;
			font-family: "Roboto Condensed";
			font-size: 154px;
			font-weight: 700;
			line-height: 1;
			margin-top: auto;
			text-shadow: 1px 1px 2px black, 0 0 1em #394791;

			@media screen and (max-width: 767px) {
				font-size: 110px;
			}
		}

		&__text {
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 26px;
			font-weight: 400;
			line-height: 33px;
			text-align: center;

			span {
				color: #394791;
				font-family: "Roboto Condensed";
				font-weight: 700;
				text-transform: uppercase;
				display: block;
			}
		}

		&__link {
			color: #394791;
			font-family: "Roboto Condensed";
			font-size: 18px;
			font-weight: 400;
			line-height: 32px;
			text-decoration: underline;
			display: flex;
			justify-content: center;
			align-items: center;
		}

		.upsale__parent {
			display: flex;

			img {
				width: 100%;
			}
		}
	}

	.tags__status {
		width: 100%;
		height: auto;
		border: 1px solid #f1f1f1;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;

		a {
			color: #394791;
			font-family: Arial;
			font-size: 12px;
			margin-right: 26px;
		}
	}

	.description {
		padding: 0px 0 20px;
		margin-bottom: 2rem;
		display: flex;
		flex-wrap: wrap;
		// border-bottom: 1px solid #eeeeee;

		&__title {
			color: #262626;
			font-family: "Roboto Condensed";
			font-size: 26px;
			font-weight: 400;
			line-height: 24px;
			width: 100%;
		}

		&__text {
			margin-top: 22px;
			color: #262626;
			font-family: Arial;
			font-size: 14px;
			line-height: 22px;
		}
	}

	.sidebar {
		display: none;
	}

	.price__dashed,
	&__items .price__dashed {
		position: relative;

		.catalog__price {
			color: #d82300;
			flex-wrap: wrap;
			// width: 50%;
			height: 100%;
			align-items: flex-start;
			position: relative;
		}


		.price__absolute {
			position: absolute;
			left: 0;
			color: #999999;
			font-family: "Roboto Condensed";
			font-size: 13px;
			font-weight: 400;
			line-height: 1;
			text-decoration: line-through;
			left: 0;
			top: unset;
			bottom: 0;
		}
	}


	.load-more {
		color: #394791;
		font-family: "Roboto Condensed";
		font-size: 14px;
		font-weight: 400;
		line-height: 24px;
		text-transform: uppercase;
		margin: 0 auto 9px;
		white-space: nowrap;
		min-width: 100px;
		border-radius: 3px;
		border: 1px solid #f1f1f1;
		text-align: center;
		text-decoration: none !important;
		padding: 5px 46px;
	}

	.product-day {
		border: 2px solid #6da970;
		background-color: #ffffff;
		display: flex;
		padding: 36px 36px 27px;
		margin-bottom: 26px;

		&__social {
			display: flex;
			color: #333333;
			font-family: Arial;
			font-size: 9px;
			line-height: 18px;
			margin-left: 16px;
			position: relative;

			.item {
				// margin-right: 23px;
				display: flex;
				flex-direction: column;
				align-items: center;


				// &:last-child {
				// 	margin-right: 0;
				// }

				&.js-share {
					position: relative;
				}

				&.compare {
					padding-top: 18px;
					background: url("../images/compare.svg") no-repeat;
					background-position: top center;
					flex-grow: 1;
				}

				&.favorite {
					padding-top: 18px;
					background: url("../images/fav inactive.svg") no-repeat;
					background-position: top center;
					max-width: 53px;
					white-space: nowrap;

					&.active {
						background: url("../images/fav active.svg") no-repeat;
						background-position: top center;
						background-size: 20px;
					}
				}
			}

			.share__items {
				padding: 5px 10px;
				top: 100%;
				position: absolute;
				display: none;
				border: 1px solid #e6e6e6;
				z-index: 9999;
				background-color: #fff;
				box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.4);
				flex-direction: column;

				a {
					margin: 4px 0;
					width: 100%;
					transition: all 0.3s ease;
					display: flex;
					align-items: center;
					font-size: 14px;
					color: #333333;
					text-decoration: none !important;

					&:hover {

						color: #394791;

					}
				}

				img {
					width: 24px;
					height: 24px;
					height: auto;
					margin-right: 10px
				}



			}

			p {
				margin-top: 4px;
				margin-bottom: 0;
				color: #333333;
				font-family: Arial;
				font-size: 9px;
				line-height: 18px;
				text-decoration: none;
			}
		}

		&__title {
			font-family: "Roboto Condensed";
			font-size: 36px;
			font-weight: 400;
			color: #394791;
			margin-bottom: 38px;
		}

		&__subtitle {
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 18px;
			font-weight: 400;
			line-height: 24px;
			margin-bottom: 6px;
			text-decoration: none !important;
		}

		&__item {
			display: flex;

			&_right {
				flex-direction: column;
				// margin-left: 100px;
				padding-top: 4px;
			}

			&_left {
				flex-grow: 1;
			}
		}

		&__el {
			display: flex;
			flex-direction: column;
			width: 205px;
		}

		.ratings__none {
			background-size: 17px 17px;
		}

		.ratings__number {
			margin-left: 8px;
			color: #333333;
			font-family: Arial;
			font-size: 14px;
			font-weight: 700;
			line-height: 24px;
		}

		.reviews__all {
			color: #394791;
			font-family: Arial;
			font-size: 14px;
			line-height: 24px;
			margin-left: 6px;
		}

		&__reviews {
			margin-bottom: 51px;
		}

		&__pic {
			// margin-left: 89px;
			align-items: baseline;
			display: flex;
			width: 203px;
			justify-content: center;
			margin: 0 auto;
			max-height: 205px;

			img {
				max-width: 100%;
				max-height: 100%;
			}
		}

		&__price {
			color: #d82300;
			font-family: "Roboto Condensed";
			font-size: 36px;
			font-weight: 400;
			line-height: 24px;
			text-transform: uppercase;
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			flex-grow: 1;

			.price__absolute {
				color: #999999;
				font-family: "Roboto Condensed";
				font-size: 16px;
				font-weight: 400;
				text-decoration: line-through;
				width: 100%;
				margin-top: 8px;
			}

			i {
				font-style: normal;
				margin-left: 7px;
			}
		}

		&__key {
			color: #333333;
			font-family: Arial;
			font-size: 14px;
			margin-bottom: 32px;
		}

		&__button {
			margin-top: 15px;
			display: flex;

			a {
				text-decoration: none !important;
			}
		}


		&__link {
			color: #394791;
			font-family: "Roboto Condensed";
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;
			text-transform: uppercase;
			border-radius: 3px;
			border: 1px solid #394791;
			padding: 4px 15px;
			margin-right: 21px;

		}

		&__buy {
			color: white;
			font-family: "Roboto Condensed";
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;
			text-transform: uppercase;
			border-radius: 3px;
			background-color: #394791;
			border: 1px solid #394791;
			padding: 4px 15px;
			margin-right: 16px;
			display: flex;
			align-items: center;

			&:before {
				content: '';
				background-image: url(../images/shopping-cart.svg);
				width: 16px;
				height: 16px;
				background-size: 16px;
				margin-right: 8px;
			}
		}

		.ya-share2__title {
			display: block !important;
		}

		.ya-share2__item {
			display: block !important;
			opacity: 1 !important;
		}

		.ya-share2__list {
			display: flex;
			flex-direction: column;
		}
	}

	.special-offers__buy {
		width: 112px;
		height: 34px;

		&:before {
			content: '';
			background-image: url(../images/shopping-cart.svg);
			width: 16px;
			height: 16px;
			background-size: 16px;
			margin-right: 8px;
		}
	}

	.popular-sections {
		margin-bottom: 14px;

		// .popular-sections__title

		&__title {
			color: #212121;
			font-family: "Roboto Condensed";
			font-size: 30px;
			font-weight: 400;
			margin-top: 5px;
			margin-bottom: 32px;
		}

		// .popular-sections__items

		&__items {
			display: flex;
		}

		// .popular-sections__item

		&__item {
			width: calc(100%/3);

			&:nth-of-type(2) {
				margin: 0 24px;
			}

			&:hover {
				.popular-sections__subtitle {
					color: #394791;
				}
			}
		}

		// .popular-sections__pic

		&__pic {
			height: 150px;
			margin-bottom: 16px;

			img {
				width: 100%;
			}
		}

		// .popular-sections__tile

		&__subtitle {
			color: #212121;
			font-family: "Roboto Condensed";
			font-size: 22px;
			font-weight: 400;
			line-height: 24px;
			padding-left: 25px;
		}
	}

	.reviews {

		// .reviews__title

		&__title {
			margin-bottom: 9px;
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 26px;
			font-weight: 400;
			line-height: 24px;
		}

		// .reviews__top

		&__top {
			height: 55px;
		}


		.reviews-list__field {
			.ratings {
				padding-left: 0;

				&__star {
					background-size: 12px 10px;
					width: 12px;
					height: 10px;
					margin-right: 2px;
				}
			}

		}

		.ratings {
			align-items: center;
			padding-left: 5px;

			// .ratings_small

			&_small {}

			// .ratings__star

			&__star {
				background-size: 16px;
				width: 16px;
				height: 16px;
				margin-right: 4px;
			}

			// .ratings__none

			&__none {}

			&__number {
				margin-left: 8px;
				color: #333;
				font-family: Arial;
				font-size: 14px;
				font-weight: 700;
				line-height: 24px;
			}
		}

		.reviews-list__item {
			padding: 16px 0 10px;

			.reviews-list__field:last-of-type {
				padding-bottom: 14px;
			}

		}

		.reviews-list {
			padding-bottom: 7px;
			border-bottom: 0;

			&_header {
				border-bottom: 1px solid #eeeeee;
				border-top: 1px solid #eeeeee;
			}

			&:nth-child(3) {
				padding-bottom: 5px;
			}

			&:nth-child(4) {
				padding-bottom: 2px;
			}
		}

		&__col {
			&:first-child {
				width: 184px;
				flex-shrink: 0;
				color: #333333;
				font-family: Arial;
				font-size: 14px;
				font-weight: 700;
			}

			&[itemprop="author"] {
				color: #333333;
				font-family: "Roboto Condensed";
				font-size: 20px;
				font-weight: 400;
				line-height: 24px;
			}

			&_date {
				font-size: 14px;
				font-weight: normal;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.71;
				letter-spacing: normal;
				text-align: left;
				color: #333333;
				margin-left: 1rem;
			}

