.about-us {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 63px;
  flex-direction: row;


  h3 {
    max-width: 272px;
    margin-top: 0 !important;
    margin-bottom: 0 !important;
    font-size: 23px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 30px;
    letter-spacing: normal;
    text-align: left;
    color: #333333;
    width: 100%;
    margin-right: 32px;
  }

  img {
    margin-right: 28px;
    width: 66%;
    margin-bottom: 50px;
  }

  p {
    width: calc(34% - 28px);
    color: #333333;
    font-family: Arial;
    font-size: 14px;
    line-height: 22px;
    text-align: left;
    z-index: 99;
    margin-top: 0 !important;
    margin-bottom: 16px;


    &~p {
      width: calc(100% - 304px);
    }

  }

  iframe {
    margin: 39px 0 42px;
  }


  @media screen and (max-width: 768px) {

    margin-bottom: 4px;

    iframe {
      max-width: 482px;
      width: 100%;
      max-height: 245px;
      margin: 21px 0 38px;
    }


    img {
      margin-right: 26px;
      width: 48%;
      max-height: 181px;
      margin-bottom: 43px;
    }

    h3 {
      max-width: 218px;
      margin-right: 24px;
      color: #333;
      font-size: 20px;
      font-weight: 400;
      line-height: 30px;

    }

    p {
      width: calc(52% - 26px);
      font-size: 14px;
      line-height: 22px;


      &~p {
        width: calc(100% - 242px);
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

  }

  @media screen and (max-width: 480px) {
    flex-direction: column;

    iframe {
      height: 150px;
      margin: 0px 0 24px;
    }


    img {
      width: 100%;
      margin-bottom: 1rem;
    }

    h3 {
      max-width: unset;
      margin-right: 0;
      margin-bottom: 18px !important;
      font-size: 16px !important;
      font-weight: 400 !important;
      line-height: 24px !important;
    }

    p {
      width: 100%;

      margin-bottom: 20px;

      &~P {
        width: 100%;
      }

      &:last-child {
        margin-bottom: 22px;
      }
    }

    &__text {
      color: #262626;

      font-size: 13px;
      line-height: 20px;
    }
  }
}